import React, { useState } from 'react';

import { makeStyles } from '@material-ui/core';
import CssBaseline from '@material-ui/core/CssBaseline';

import { AppHeader } from './AppHeader';
import { AppSideBarMini } from './AppSideBar';
import AppLayoutStyle from './AppLayout.Style';

const useStyles = makeStyles(AppLayoutStyle);

function AppLayout(props) {
  const classes = useStyles();
  const [drawerOpen, setDrawerOpen] = useState(false);
  const { children, sideBarItems, headerTitle, headerContent } = props;
  //console.log('children: ', children);
  const handleDrawerOpen = () => {
    setDrawerOpen(true);
  };
  const handleDrawerClose = () => {
    setDrawerOpen(false);
  };
  const handleDrawerToggle = () => {
    setDrawerOpen(!drawerOpen);
  };

  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppHeader
        appTitle={headerTitle}
        appHeaderContent={headerContent}
        drawerOpen={drawerOpen}
        //onDrawerOpen={handleDrawerOpen}
        onDrawerOpen={handleDrawerToggle} //for clipped appbar
      />
      <AppSideBarMini
        sideBarItems={sideBarItems}
        drawerOpen={drawerOpen}
        onDrawerClose={handleDrawerClose}
        onDrawerOpen={handleDrawerOpen} //for clipped appbar
      />
      <main className={classes.content}>
        <div className={classes.toolbar} />
        <div className={classes.contentPage}>{children}</div>
      </main>
    </div>
  );
}

export default AppLayout;
