import React from 'react';
import clsx from 'clsx';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { Drawer } from '@material-ui/core';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';

import AppSideBarMiniStyle from './AppSideBar.Style';
import AppSideBarList from './AppSideBarList';

//import DrawerList from '../../DrawerListEx';

//const useStyles = makeStyles(AppSideBarMiniStyle);

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: 'nowrap',
  },
  drawerOpen: {
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerClose: {
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    overflowX: 'hidden',
    width: theme.spacing(7),
    [theme.breakpoints.up('sm')]: {
      width: theme.spacing(9),
    },
  },
  toolbar: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
  },
}));

const AppSideBarMini = (props) => {
  const classes = useStyles();
  const { drawerOpen, onDrawerClose, onDrawerOpen, sideBarItems } = props;

  const theme = useTheme();
  //console.log("theme: ", theme);
  const isDeviceMdOrUp = useMediaQuery(theme.breakpoints.up('md'));

  //console.log("isDeviceMdOrUp: ", isDeviceMdOrUp);

  //console.log("drawerOpen: " + drawerOpen);

  const DrawerComp = (props) => {
    const classes = useStyles(props);
    const { anchor } = props;
    //console.log(classes);
    return (
      <SwipeableDrawer
        className={clsx(classes.tempDrawer)}
        anchor={anchor}
        open={drawerOpen}
        onClose={onDrawerClose}
        onOpen={onDrawerOpen}>
        {props.children}
      </SwipeableDrawer>
    );
  };

  return (
    <>
      {isDeviceMdOrUp ? (
        <Drawer
          variant='permanent'
          className={clsx(classes.drawer, {
            [classes.drawerOpen]: drawerOpen,
            [classes.drawerClose]: !drawerOpen,
          })}
          classes={{
            paper: clsx({
              [classes.drawerOpen]: drawerOpen,
              [classes.drawerClose]: !drawerOpen,
            }),
          }}
          open={drawerOpen}>
          <div className={classes.toolbar}>
            <IconButton onClick={onDrawerClose}>
              <ChevronLeftIcon />
            </IconButton>
          </div>
          <Divider />
          <AppSideBarList miniMode={!drawerOpen} title='' {...props} />
          <Divider />
        </Drawer>
      ) : (
        <DrawerComp anchor='left'>
          <div className={classes.toolbar}>
            <IconButton onClick={onDrawerClose}>
              <ChevronLeftIcon />
            </IconButton>
          </div>
          <Divider />
          <AppSideBarList
            miniMode={!drawerOpen}
            title=''
            sideBarItems={sideBarItems}
          />
          <Divider />
        </DrawerComp>
      )}
    </>
  );
};

export default AppSideBarMini;
