import * as appRoutes from './appRoutes';

const defaultHandler = (e) => {
  console.log(
    'event: ' +
      e.type.toUpperCase() +
      ' e.target.value: ' +
      (e.target && e.target.value)
  );
};

export const headerBarOptions = {
  showSearchBox: false,
  showAlertsButton: true,
  alertsCount: 1,
  showSettingsButton: true,
  showLogoutButton: true,
  onClickAlertsButton: defaultHandler,
  onClickSettingsButton: defaultHandler,
  onChangeSearchBox: undefined,
  onKeyPressSearchBox: undefined,
  onClickLogoutButton: defaultHandler
};

const ModuleConfig = {
  appSideBarItems: appRoutes.AU_SIDEBAR_ITEMS,
  headerBarOptions: headerBarOptions,
  appRoutes: appRoutes.getAppRoutes()
  //getAppRoutesFn: appRoutes.getAppRoutes
};


ModuleConfig.destroy = (event) => {
  const status = {
    event: event,
    status: 'OK',
  };

  try {
    __cleanup(ModuleConfig, status);
    localStorage.setItem('AUCleanStatus', JSON.stringify(status));
  } catch (error) {
    console.error('ModuleConfig.destroy: cleanup error: ', error);
  }
};

function __cleanup(obj, status) {
  //console.log('__cleanup :', Object.keys(obj));
  Object.keys(obj).forEach((key) => {
    if (__destroy(obj[key])) {
      status[key] = 'DONE';
    }
  });
}

function __destroy(obj) {
  if ('destroy' in obj && typeof obj.destroy === 'function') {
    obj.destroy();
    return true;
  }
  return false;
}

export default ModuleConfig;
