import React from 'react';
import './MainApp.css';

import ThemeProvider from '../../providers/theme/theme.provider';
import { createTheme as createMuiTheme } from '@material-ui/core/styles';
import { AppLayout } from '../../components/AppLayout';

import AUModuleConfig from './appBootstrap';

import { RouteProvider } from '../../providers/route';

import simpleLight from './themes/simpleLight.json';
import simpleDark from './themes/simpleDark.json';

//const theme = createMuiTheme(simpleDark);
const theme = createMuiTheme(simpleLight);

if (document) {
  document.title = 'RK WebApps';
}

function WebAppApp(props) {
  React.useEffect(() => {
    const closeHandler = (ev) => {
      ev.preventDefault();
      return AUModuleConfig.destroy('browser.beforeunload');
    };

    console.log('app bootstraping...');
    window && window.addEventListener('beforeunload', closeHandler);

    return () => {
      //app cleanup on refreshing page or unmounting
      console.log('app cleaning up started');
      AUModuleConfig.destroy('useEffect-cleanup');
      window && window.removeEventListener('beforeunload', closeHandler);
      console.log('app cleaning up finished');
    };
  }, []);

  return (
    <>
            <RouteProvider>
              <WebAppLocale {...props}>
                <WebApp {...props} />
              </WebAppLocale>
            </RouteProvider>
    </>
  );
}

function WebAppLocale(props) {
  const { children } = props;
  return (
      <ThemeProvider value={theme}>{children}</ThemeProvider>
  );
}

function WebApp(props) {
  return (
    <AppLayout
      headerTitle={document.title}
      //headerContent={<HeaderBar options={AUModuleConfig.headerBarOptions} />}
      sideBarItems={AUModuleConfig.appSideBarItems}
    >
      {AUModuleConfig.appRoutes}
    </AppLayout>
  );
}

export default WebAppApp;
