import React from "react";
import { Link as RouterLink } from "react-router-dom";

export function createLinkElement(linkCompProps) {
  const { ...restProps } = linkCompProps;
  return React.forwardRef((itemProps, ref) => (
    <RouterLink ref={ref} {...restProps} {...itemProps} />
  ));
}

