import React from 'react';
import clsx from 'clsx';
import {
  makeStyles,
  useTheme,
  useMediaQuery,
  AppBar,
  Toolbar,
  Typography,
  IconButton,
  CssBaseline
} from '@material-ui/core';

import MenuIcon from '@material-ui/icons/Menu';

import AppHeaderStyle from './AppHeader.Style';

import RPLogoReversedSmall from './../../../images/RPLogoReversedSmall.png';

const useStyles = makeStyles(AppHeaderStyle);

function AppHeader(props) {
  const classes = useStyles();
  const { drawerOpen, appTitle, onDrawerOpen, appHeaderContent } = props;
  const theme = useTheme();
  const isDeviceSmOrUp = useMediaQuery(theme.breakpoints.up('sm'));

  //const drawerOpen = this.state.open;
  //console.log('drawerOpen', drawerOpen);
  //console.log('AppHeader - theme: ', theme);
  //console.log("AppHeader - theme text: ", JSON.stringify(theme));

  const logo = {
    img: RPLogoReversedSmall,
    title: appTitle
  };
  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar
        position='fixed'
        className={clsx(classes.appBar, {
          [classes.appBarShift]: drawerOpen
        })}
      >
        <Toolbar style={{ display: 'flex', justifyContent: 'space-between' }}>
          <div>
            <IconButton
              color='inherit'
              aria-label='Open drawer'
              onClick={onDrawerOpen}
              edge='start'
              // className={clsx(classes.menuButton, {
              //   [classes.hide]: drawerOpen
              // })}
            >
              <MenuIcon />
            </IconButton>
          </div>
          <div>
            <Typography variant='h6' noWrap>
              {appTitle}
            </Typography>
          </div>
          <div>{isDeviceSmOrUp && appHeaderContent}</div>
        </Toolbar>
      </AppBar>
    </div>
  );
}

AppHeader.propTypes = {
  //classes: PropTypes.object.isRequired
};

export default AppHeader;
