const AppSideBarListStyle = theme => ({
  root: {
    width: "100%",
    backgroundColor: theme.palette.background.paper,
    padding: 0

    //TODO: the following two lines can be used to reserve 20% space for permanent menu items e.g. About or version info
    //maxHeight: "80%",
    //overflow: "auto"
  },

  listContainer: {
    display: "flex",
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "flex-start"
  },

  listIconCol: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center",
    paddingLeft: theme.spacing(1),
    minWidth: "48px"
    //color: "red",
  },

  listMenuItemIcon: {
    minWidth: "48px",
    paddingLeft: theme.spacing(2)
    //paddingLeft: theme.spacing(3),
  },

  listMenuSubItemIcon: {
    //maxWidth: '80px',
    paddingLeft: theme.spacing(4),
    paddingRight: theme.spacing(2)
  },

  listMenuCol: {
    display: "flex",
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "flex-start",
    flexGrow: 1,
    paddingLeft: theme.spacing(0)
    //color: "red",
  },

  listMenuMini: {
    display: "flex",
    flexDirection: "column",
    alignItems: "flex-start",
    justifyContent: "center",
    //color: "blue",
    fontSize: "0.6rem",
    fontWeight: "500",
    minWidth: theme.spacing(4)
  }
});

export default AppSideBarListStyle;
