export function getAppRoutes(props) {
  //console.log('getAppRoutes.props: ', props);
  return (
    <RouterSwitch>
      {/* <PublicRoute exact path={`${AU_APP_ROOT}/automm`}>
        <AutoMM.ConfigPage />
      </PublicRoute> */}

      <PublicRoute path={`${AU_APP_ROOT}/home`} exact={true}>
        {getTestContent('HOME')}
      </PublicRoute>
      <PublicRoute path={`${AU_APP_ROOT}/configform/`} exact={true}>
        {getTestContent('CONFIG')}
      </PublicRoute>
    </RouterSwitch>
  );
}

