import React from "react";
import { Tooltip, makeStyles } from "@material-ui/core";
import clsx from "clsx";
import ListSubheader from "@material-ui/core/ListSubheader";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import Collapse from "@material-ui/core/Collapse";
import ArrowDropDownIcon from "@material-ui/icons/ArrowDropDown";
import ArrowRightIcon from "@material-ui/icons/ArrowRight";
import ViewQuiltSharpIcon from "@material-ui/icons/ViewQuiltSharp";

import AppSideBarListStyle from "./AppSideBarList.Style";

const useStyles = makeStyles(AppSideBarListStyle);

function ListItemGroup(props) {
  const { listItemDef } = props;
  const classes = useStyles(props);
  const {
    miniMode,
    onSelectedItemChange,
    selectedItemID,
    isSubListItem
  } = props;
  const { isItemExpanded, onItemExpanded } = props;

  //console.log('isItemCollapsed: ' + isItemCollapsed);
  //console.log("opened", open);

  //console.log('listItemDef: ', listItemDef);

  const iconFontSize = miniMode ? "small" : "large";
  const hasSubItems =
    listItemDef.subListItems && listItemDef.subListItems.length > 0;

  const iconClass = listItemDef.iconClass ? (
    listItemDef.iconClass
  ) : (
    <ViewQuiltSharpIcon />
  );

  const linkComponent = listItemDef.linkComponent;
  //console.log('linkComponent: ', linkComponent);

  const handleClick = (event, cbFn, selectedItemID) => {
    if (hasSubItems) {
      onItemExpanded && onItemExpanded(!isItemExpanded);
    } else {
      //console.log("onSelectedItemChange(selectedItemID)", selectedItemID);
      onSelectedItemChange(selectedItemID);
    }

    if (cbFn) {
      cbFn(event);
    }
  };

  const listItemIconClasses = isSubListItem
    ? miniMode
      ? classes.listMenuItemIcon
      : classes.listMenuSubItemIcon
    : classes.listIconCol;
  const listItemTextClasses = isSubListItem
    ? clsx(classes.listMenuCol, classes.listMenuSubItemIcon)
    : classes.listMenuCol;

  return (
    <Tooltip
      title={miniMode ? listItemDef.longLabel : ""}
      arrow
      placement="right"
    >
      <ListItem
        key={"L." + listItemDef.ID}
        button
        disableGutters={true}
        component={linkComponent}
        onClick={e =>
          handleClick(
            e,
            listItemDef.onClick && listItemDef.onClick.bind(listItemDef),
            listItemDef.ID
          )
        }
        selected={selectedItemID === listItemDef.ID}
      >
        <ListItemIcon className={listItemIconClasses}>{iconClass}</ListItemIcon>
        <div className={listItemTextClasses}>
          {!miniMode && <ListItemText primary={listItemDef.longLabel} />}
          {hasSubItems &&
            (isItemExpanded ? (
              <ArrowDropDownIcon fontSize={iconFontSize} />
            ) : (
              <ArrowRightIcon fontSize={iconFontSize} />
            ))}
        </div>
      </ListItem>
    </Tooltip>
  );
}

export function AppSideBarList(props) {
  const classes = useStyles(props);
  const [open, setOpen] = React.useState(true);
  const { miniMode, sideBarItems } = props;
  const [selectedItemID, setSelectedItemID] = React.useState("");

  //console.log("AppSideBarList.miniMode: ", props.miniMode);
  //console.log("theme", props.theme);
  //console.log("AppSideBarList.props", props);

  return (
    <div className={classes.root}>
      {sideBarItems.map(sbItems => {
        return (
          <List
            key={sbItems.listID}
            component="div"
            aria-labelledby="nested-list-subheader"
            subheader={
              <ListSubheader
                component="div"
                id="nested-list-subheader"
                disableGutters={false}
              >
                {miniMode ? sbItems.listShortLabel : sbItems.listLongLabel}
              </ListSubheader>
            }
          >
            {sbItems.listItems.map((item, i) => {
              return (
                <div key={`${item.ID}.${i}`}>
                  <ListItemGroup
                    listItemDef={item}
                    {...props}
                    onSelectedItemChange={setSelectedItemID}
                    selectedItemID={selectedItemID}
                    isSubListItem={false}
                    isItemExpanded={open}
                    onItemExpanded={setOpen}
                  />
                  {item.subListItems && (
                    <Collapse in={open} timeout="auto" unmountOnExit>
                      <List component="div" disablePadding>
                        {item.subListItems &&
                          item.subListItems.map((subItem, idx) => {
                            return (
                              <ListItemGroup
                                key={`${subItem.ID}.${idx}`}
                                listItemDef={subItem}
                                {...props}
                                onSelectedItemChange={setSelectedItemID}
                                selectedItemID={selectedItemID}
                                isSubListItem={true}
                              ></ListItemGroup>
                            );
                          })}
                      </List>
                    </Collapse>
                  )}
                </div>
              );
            })}
          </List>
        );
      })}
    </div>
  );
}

export default AppSideBarList;
